    .proj-left img {margin-left: -43px; margin-top: 23px;}
    p {font-size: 0.875rem;}
    .proj-link a { font-size: 1rem !important;}
    .intro p {font-size: 1.10rem !important;}
    h1 {font-size: 2.5rem !important;}
    h2 {font-size: 1.75rem !important; margin-bottom: 0 !important;}


    .proj-left {
        width: 100%;
    }
    .proj-right {
        width: 100%;
        float: left !important;
    }
    .proj-right img {margin-left:-40px; margin-top:23px; }
    .colorbox {display: none;}



    .my-info {
        margin-right: 0% !important;
        width: 40%;
    }
    .box-text p {
        font-size: 0.8rem;
    }
     
    
}

